<template>
    <v-layout>
        <v-card contextual-style="dark">
            <span slot="header">
                {{ $t('general.welcome') }}
            </span>
            <div slot="body">
                <p>step: {{ step }}</p>
            </div>
            <div slot="footer">Make'em better!</div>
        </v-card>
    </v-layout>
</template>

<script>
/* ============
 * Home Index Page
 * ============
 *
 * The home index page.
 */

import VLayout from '@/layouts/Default.vue'
import VCard from '@/components/Card.vue'

export default {
    /**
     * The name of the page.
     */
    name: 'HomeIndex',

    /**
     * The components that the page can use.
     */
    components: {
        VLayout,
        VCard,
    },
    data() {
        return {
            step: 0,
            loop: true,
        }
    },
    methods: {
        fetchStep: function () {
            const z = this
            z.$http
                .get('/api/step')
                .then(function (response) {
                    return response.json()
                })
                .then(function (myJson) {
                    z.step = myJson.step || 1
                })
                .catch(function (error) {
                    alert(error)
                })
        },
        timer(timeout) {
            const z = this
            const t = timeout || 3000
            setTimeout(function () {
                if (z.loop) {
                    z.fetchStep().then(z.timer.bind(z))
                }
            }, t)
        },
    },
    created() {
        this.timer(0)
    },
}
</script>
